*{
  font-family: 'Inter', sans-serif;
}

html, body{
  margin: 0;
  background-image: url('./img.jpg');
  background-size: cover;
  height: 100vh;
}


.home{
  text-align: center;
  position: absolute;
  width: 100%;
  text-align: center;
  top: 30%;
}

.home h3{
  padding: 0 20px;
}


.form-container{
  display: flex;
}

.form{
  min-width: 310px;
  padding-left: 20px;
}


#numLang, #numPage{
  padding: 2px;
  margin: 10px 10px 0 10px ;
  border: none;
  border-radius: 3px;
  text-align: center;
  font-weight: 600;
}

.pages{
  margin-left: 15px;
  margin-right: 15px;
}

.btnPresupuesto{
  cursor: pointer;
  border: none;
  background-color: rgb(64, 64, 214);
  color: white;
  padding: 10px;
  border-radius: 5px;
  box-shadow: 2px 2px 2px 1px gray;
  font-weight: 500;
}

.btnPresupuesto:hover{
  background-color: rgb(10, 10, 154);
}

.btnPresupuesto:active{
  transform: scale(.95);
}

label{
  padding: 3px;
  text-shadow: 1px 1px 2px #fff;
}



.link{
  color: rgb(255, 255, 255);
  text-decoration: none;
  text-shadow: 1px 1px 2px rgb(221, 221, 221);
  font-weight: 500;

}

.link:hover{
  color: rgb(211, 218, 255);
}

.info {
  background-color: gray;
  margin-left: 7px;
  border: none;
  border-radius: 10px;
  padding: 0 7px;
  color: white;
  cursor: pointer;
}

.info:hover{
  background-color: rgb(74, 74, 74);
}

.btnPlusMinus{
  border: none;
  color: white;
  background-color: orange;
  border-radius: 3px;
  cursor: pointer;
  width: 20px;
  height: 22px;
}
.btnPlusMinus:hover {
  background-color: rgb(250, 109, 0);
}

@media (max-width: 700px) {
  .form-container{
    flex-direction: column;
  }


}

